body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  background-color: white;
}

.Auth-form-container {
  display: grid;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.Auth-form {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  padding-top: 30px;
  padding-bottom: 20px;
  margin-top: -800px;
  border-radius: 8px;
  background-color: white;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
}

.app-container {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  padding: 1rem;
}

table {
  margin-left: 10px;
  margin-right: 10px;
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  border: 1px solid #ffffff;
  text-align: left;
  padding: 8px;
  font-size: 32px;
}

th {
  background-color: rgb(117, 201, 250);
}

td {
  background-color: rgb(205, 235, 253);
}

form {
  display: flex;
  grid-gap: 7px;
  gap: 7px;
}

form td:last-child {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-evenly;
}

form * {
  font-size: 28px;
}

#inputForm {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
}

#logout {
  margin-top: -150px;
  margin-bottom: 100px;
  margin-left: 5%;
  background: indianred;
  outline-color: indianred;
}

#welcomeMessage {
  text-align: center;
  margin: 20px auto;
  font-size: 35px;
}

#bookTitles {
  width: 400px;
}

#ratingBox {
  width: 180px;
}

#loading {
  margin-top: 100px;
  margin-left: 50%;
}



.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 540px;
  height: 100%;
  color: white;
}

#colorStrip {
  width: 50%; height: 100px;
  margin-right: auto;
  margin-left: auto;
  border-style: solid;
  border-color: white;
  background: #3d70fa;
  border-radius: 30px;
  text-align: center;
}
